* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

.header {
  width: 100%;
  min-height: 100vh;
  background-image: url(../img/home-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}
.row__header {
  height: 100vh;
}
.row__conteniedo {
  padding-bottom: 30px;
}
.titulo__h1 {
  color: #fff;
  font-family: "Poppins", helvetica;
}
.header p {
  color: #fff;
  font-size: 13px;
  letter-spacing: 5px;
  margin-top: 0;
  margin-bottom: 30px;
  text-transform: capitalize;
  font-weight: 500;
}
#navbar {
  background: transparent;
}
.navbar-light .navbar-brand {
  color: #fff;
}
.navbar-light .navbar-nav .nav-link,
.navbar-light .navbar-nav .show > .nav-link {
  color: #fff;
}
.list-unstyled {
  padding-left: 0;
  list-style: none;
}
.list-unstyled a {
  color: #fff;
  font-size: 15px;
}
.list-unstyled li {
  float: left;
  margin-right: 20px;
}
footer {
  margin-top: 60px;
}
.subcontenidoModal {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100%;
  flex-wrap: wrap;
}
.listas__subconteniedo {
  margin-right: 14px;
  width: 24%;
}
.list-group-item.active {
  background-color: #901508;
  border-color: #901508;
}
